import React, { useState, useEffect } from 'react';
import 'bootstrap/dist/css/bootstrap.min.css';
import './App.css';
import MovieList from './components/MovieList';
import MovieListHeading from './components/MovieListHeading';
import SearchBox from './components/SearchBox';
import AddFavourites from './components/AddFavourites';
import RemoveFavourites from './components/RemoveFavourites';
import LoadingSpinner from "./components/loading";
import axios from 'axios';
import Navbar from './components/navbar';

const App = () => {
	const [movies, setMovies] = useState([]);
	const [favourites, setFavourites] = useState([]);
	const [searchValue, setSearchValue] = useState('');
	const [isLoading, setIsLoading] = useState('');

	const getMovieRequest = async (searchValue) => {
		const url = "/api";
		setIsLoading("new");
		const response = await fetch(url);
		const responseJson = await response.json();

		setIsLoading("");
		setMovies(responseJson.movies);
	
	};

	useEffect(() => {
		getMovieRequest();
	},[]);

	useEffect(() => {
		const movieFavourites = JSON.parse(
			localStorage.getItem('react-movie-app-favourites')
		);

		if (movieFavourites) {
			setFavourites(movieFavourites);
		}
	}, []);

	const saveToLocalStorage = (items) => {
		localStorage.setItem('react-movie-app-favourites', JSON.stringify(items));
	};

	const addFavouriteMovie = async (movie) => {
		console.log("MOIVE_ID:" + movie.movie_id)
		const bodyFormData = new FormData();
		bodyFormData.append('movie_id', movie.movie_id);
		setIsLoading("add");
		const response = await axios({method:"POST", url:'/api', data:bodyFormData,  headers: { "Content-Type": "multipart/form-data" }})

		const responseJson = response.data;
		console.log(responseJson)

		setIsLoading("");
		setFavourites(responseJson.recommended);
		saveToLocalStorage(responseJson.recommended);
	};


	const removeFavouriteMovie = (movie) => {
		const newFavouriteList = favourites.filter(
			(favourite) => favourite.movie_id !== movie.movie_id
		);
		window.location.href = movie.site_url;
		setFavourites(newFavouriteList);
		saveToLocalStorage(newFavouriteList);
	};

	const render_movie = (movies,addFavouriteMovie,AddFavourites) => {
		return (
			<MovieList
					movies={movies}
					handleFavouritesClick={addFavouriteMovie}
					favouriteComponent={AddFavourites}
			/>
		)
	}

	
	return (
		<div className='container-fluid movie-app'>
			<Navbar/>
			<div className='row d-flex align-items-center mt-4 mb-4'>
				<MovieListHeading heading='Select favorite Movie' />
				{/* <SearchBox searchValue={searchValue} setSearchValue={setSearchValue} /> */}
			</div>
			<div className='row'>
				{ isLoading === "new" ? <LoadingSpinner /> :  render_movie(movies, addFavouriteMovie, AddFavourites)}
			</div>
			<div className='row d-flex align-items-center mt-4 mb-4'>
				<MovieListHeading heading='Recommended Movies' />
			</div>
			<div className='row'>
				{  isLoading === "add"? <LoadingSpinner /> : render_movie(favourites, removeFavouriteMovie, RemoveFavourites)}
			</div>
		</div>
	);
};

export default App;
